h1 {
  color: rgb(0, 6, 97);
  font-family: Arial, Helvetica, sans-serif;
  text-transform: uppercase;
  font-size: 36px;
}

h2 {
  color: rgb(230, 0, 88);
  font-family: Arial, Helvetica, sans-serif;
}

.filter {
  font-weight: bold;
  font-family: "Lucida Console", "Courier New", monospace;
}

.person_form {
  font-weight: bold;
  font-family: "Lucida Console", "Courier New", monospace;
}

.input {
  border: 1px solid rgb(78, 90, 255);
  border-radius: 4px;
  padding: 4px 12px;
  margin-left: 6px;
}

.button {
  color: #fff;
  border: none;
  background-color: rgb(0, 6, 97);
  font-family: Arial, Helvetica, sans-serif;
  letter-spacing: 1px;
  border-radius: 4px;
  padding: 6px 12px;
  text-align: center;
  display: inline-block;
  font-size: 12px;
}

.button_del {
  color: #fff;
  border: none;
  background-color: rgb(230, 0, 88);
  font-family: Arial, Helvetica, sans-serif;
  letter-spacing: 1px;
  border-radius: 4px;
  padding: 6px 12px;
  text-align: center;
  display: inline-block;
  font-size: 12px;
  margin-left: 10px;
}

.person {
  color: grey;
  font-weight: bold;
  padding-top: 3px;
  padding-right: 4px;
  font-size: 15px;
  font-family: "Lucida Console", "Courier New", monospace;
}

.error {
  color: red;
  background: lightgrey;
  font-family: "Lucida Console", "Courier New", monospace;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}

.success {
  color: green;
  background: lightgrey;
  font-family: "Lucida Console", "Courier New", monospace;
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}
